<template>
  <div id='head'>
    <img src="../assets/banner.jpg">
    <h1 id="sign">{{ msg }}</h1>
    <ul id="navigateBar">
      <li class="singleNavigate" v-for="item in items">
        <router-link :to="item.link">{{ item.content }}</router-link>
      </li>
    </ul>
    <router-view name='homePage'> </router-view>
  </div>

</template>

<script>
export default {
  name: 'head',
  data () {
    return {
      msg: 'i am allen, a software engineer!',
      items: [
        {
          link: '/',
          content: ''
        },
        {
          link: '/article',
          content: ''
        },
        {
          link: '/lab',
          content: ''
        },
        {
          link: '/admin',
          content: '/'
        }
      ]
    }
  }
}
</script>

<style>
#head {
  width: 100%;
}
img {
  width: 100%;
  height: 300px;
}
#sign {
  position: absolute;
  color: white;
  top: 5px;
  left: 10px;
}
#navigateBar {
  width:auto;/*980px*/
  border:1px solid #000;/*#0001px*/
  margin:0px auto 0px auto;/*margin:0 auto*/
  height: 35px;
}
ul .singleNavigate {
  list-style: none;
  float:left;
  background: black;
}

.singleNavigate a {
  width:80px;/*80px*/
  height:35px;/*28px*/
  line-height:35px;/*28px*/
  background:black;/**/
  color:#FFF;/**/
  font-size:12px;/*12*/
  display:block;/*a*/
  text-align:center;/**/
  text-decoration:none; /**/
}

.singleNavigate a:hover{
  width:78px;
  height:33px;
  line-height:28px;
  border:1px solid ;
  background:#111;
}
</style>



